 relacionada únicamente con la atención en caso de una emergencia, de hecho, debe persistir en evitar que la situación se dé por medio de la adecuada planeación. Sin embargo, el contexto o situaciones fuera de su alcance puede desatar episodios de pánico, donde el guía también su función de conducción, como buen líder logra hacer un manejo adecuado de la situación.")]),_c('div',{staticClass:"cajon color-primario p-4 mb-4"},[_c('p',[_vm._v("Según Roca (2015), la palabra pánico:")]),_c('p',{staticClass:"mb-0"},[_vm._v("Está directamente relacionada con las emergencias, por antecedentes históricos donde la multitud o una persona al mando actúan desesperada y descontroladamente, ocasiona heridos y en casos más graves. Y esto está relacionado con que todo ser humano ante una situación de peligro real o imaginaria, responde con miedo, es una respuesta biológicamente congénita, nos protege como especie y ha permitido nuestra sobrevivencia. En términos generales sentir miedo es bueno y hasta beneficioso, porque evita que cometamos actos imprudentes, nos permite evaluar la situación de urgencia y nos prepara para la lucha o para la huida. El problema es cuando es desproporcionado y sale fuera de nuestro control nos produce ceguera psicológica es decir nos incapacita para evaluar el peligro en forma real y escoger la mejor alternativa para enfrentarlo o huir de él (s. p.). ")])])]),_c('div',{staticClass:"col-lg-6"},[_c('img',{staticClass:"fine",attrs:{"src":require("@/assets/curso/tema2/img10.png"),"alt":"Texto que describa la imagen"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mt-2"},[_c('div',{staticClass:"col-lg-1"}),_c('div',{staticClass:"col-lg-2"},[_c('img',{staticClass:"fine",attrs:{"src":require("@/assets/curso/tema2/img11.svg"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-lg-8"},[_c('p',[_vm._v("Por otro lado, la conducta de pánico provoca la ruptura de las relaciones so